import React from 'react';
import { Grid, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import ColorLensIcon from '@material-ui/icons/ColorLens';
import AppsIcon from '@material-ui/icons/Apps';
import CodeIcon from '@material-ui/icons/Code';
import { Divider } from '@material-ui/core';



const useStyles = makeStyles((theme) => ({
    itemsStyles: {
        display: 'flex',
        justifyContent: 'center',
        marginTop: theme.spacing(15)
    },
    captionStyles: {
        color: 'rgb(200,200,200)',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignContent: 'center'
    }

}));

export default function Services() {
    const classes = useStyles();
    
    return(
    <div id="services" style={{backgroundColor:"#212121"}}>
        <Grid container sm={12}>
            <Grid container sm={2}>
                <Typography style={{fontSize: '20px', marginLeft: '100px', marginTop: '100px', color: 'rgb(100, 100, 100'}}>{'~./services__________'}</Typography>

            </Grid>
            <Grid container sm={8}>
                <Typography style={{marginLeft: '100px', marginTop: '100px', color: 'rgb(100, 100, 100'}}></Typography>
                <Grid container sm={12} >
                    <Grid container sm={4}>
                        <Grid item sm={12} className={classes.itemsStyles}>
                            <AppsIcon style={{color:'#ffff8d', fontSize: 100}}/>
                        </Grid>
                        <Grid item sm={12} className={classes.captionStyles}>
                        <Typography variant='h4' className={classes.captionStyles}><br/>UI Design<br/><span style={{color: 'rgb(100,100,100)'}}>________</span></Typography>
                        <Typography variant='h6' ><br/>Adobe Xd</Typography>
                        <Typography variant='h6' >Figma</Typography>
                        </Grid>
                    </Grid>
                    <Grid container sm={4}>
                        <Grid item sm={12} className={classes.itemsStyles}>
                            <CodeIcon style={{color:'#ffff8d', fontSize: 100}}/>
                        </Grid>
                        <Grid item sm={12} className={classes.captionStyles}>
                        <Typography variant='h4' className={classes.captionStyles}><br/>App Development<br/><span style={{color: 'rgb(100,100,100)'}}>________</span></Typography>
                        <Typography variant='h6' ><br/>React Native</Typography>
                        <Typography variant='h6' >Android Studio</Typography>                        
                        
                        </Grid>
                    </Grid>

                    <Grid container sm={4}>
                        <Grid item sm={12} className={classes.itemsStyles}>
                            <ColorLensIcon style={{color:'#ffff8d', fontSize: 100}}/>
                        </Grid>
                        <Grid item sm={12} className={classes.captionStyles}>
                            <Typography variant='h4' className={classes.captionStyles}><br/>Graphics Design<br/><span style={{color: 'rgb(100,100,100)'}}>________</span></Typography>
                            <Typography variant='h6' ><br/>Adobe Photoshop</Typography>
                            <Typography variant='h6' >Adobe Illustrator</Typography>
                        </Grid>
                    </Grid>
                    
                </Grid>    
            </Grid>
            <Grid container sm={2} />
        </Grid>
        <Divider variant="fullWidth" style={{marginTop: '100px'}}/>
    </div>
    )
}