import React from 'react';
import { Grid, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import FacebookIcon from '@material-ui/icons/Facebook';
import IconButton from '@material-ui/core/IconButton';
import TwitterIcon from '@material-ui/icons/Twitter';
import InstagramIcon from '@material-ui/icons/Instagram';
import ColorLensIcon from '@material-ui/icons/ColorLens';
import AppsIcon from '@material-ui/icons/Apps';
import CodeIcon from '@material-ui/icons/Code';
import { Divider } from '@material-ui/core';



const useStyles = makeStyles((theme) => ({
    itemsStyles: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        marginTop: theme.spacing(3)
    },
    captionStyles: {
        color: 'rgb(200,200,200)',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignContent: 'center'
    }

}));

export default function Footer() {
    const classes = useStyles();
    
    return(
    <div id="contact" style={{backgroundColor:"#333333"}}>
        <Typography><br/></Typography>
        
        <Grid container sm={12}>
            
            <Grid container sm={12} className={classes.itemsStyles}>
                <Typography variant='h5' style={{color: 'rgb(150, 150, 150)'}}>Contact me<br/></Typography>
                <Typography variant='h6' style={{color: 'rgb(220, 220, 220)'}}><br/>mrifat46@gmail.com</Typography>
                <Typography variant='h5' style={{color: 'rgb(120, 120, 120)'}}>______________</Typography>

                <Grid item>
                    <Typography><br/></Typography>
                    
                    <a href="https://www.facebook.com/musfiq.rifat7" target='_blank'>
                    <IconButton aria-label="facebook" style={{color:'#84ffff'}}>
                        <FacebookIcon fontSize='large'/>
                    </IconButton>
                    </a>
                    

                    
                    <a href="https://twitter.com/Musfiq420" target='_blank'>
                    <IconButton aria-label="twitter" style={{color:'#84ffff'}}>
                        <TwitterIcon fontSize='large'/>
                    </IconButton>
                    </a>
                    
                    
                    
                    <a href="https://www.instagram.com/musfiqrifat" target='_blank'>
                    <IconButton aria-label="instagram" style={{color:'#84ffff'}}>
                        <InstagramIcon fontSize='large'/>
                    </IconButton>
                    </a>
                    
                    </Grid>
            </Grid>

       </Grid>
        <Divider variant="fullWidth" style={{marginTop: '30px'}}/>
    </div>
    )
}