import React from 'react';
import { Grid, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Divider } from '@material-ui/core';
import nikahLogo from './nikah300.png';
import quizLogo1 from './quiz1_200.png';
import quizLogo2 from './quiz2_200.png';
import quizLogo3 from './quiz3_300.png';
import tShirtLogo from './tshirt.png';



const useStyles = makeStyles((theme) => ({
    textStyles:{
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignContent: 'center',
        marginTop: theme.spacing(20),
        color: 'rgb(240, 240, 240)'
    },
    imgStyles:{
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignContent: 'center',
        marginTop: theme.spacing(20),
        color: 'rgb(240, 240, 240)'
    }


}));

export default function Portfolio() {
    const classes = useStyles();
    
    return(
    <div id="portfolio" style={{backgroundColor:"#212121"}}>
        <Grid container sm={12}>
            <Typography style={{fontSize: '20px', marginLeft: '100px', marginTop: '100px', color: 'rgb(100, 100, 100'}}>{'~./portfolio__________'}</Typography>
            
            <Grid container sm={12}>
                <Grid container sm={1} />
                <Grid container sm={5} className={classes.textStyles}>
                    <Typography style={{color: 'rgb(150, 150, 150)'}}>graphics design</Typography>
                    <Typography style={{color: 'rgb(120, 120, 120)'}}>_______________</Typography>
                    <Typography variant='h3'><br/>nikahLogo</Typography>
                    <Typography variant='h5' style={{color: 'rgb(200, 200, 200)'}}><br/>designed a logo for Nikah group</Typography>
                
                
                </Grid>
                <Grid container sm={5} className={classes.textStyles}>
                    <img src={nikahLogo} />
                </Grid>
                <Grid container sm={1} />
            </Grid>

            <Grid container sm={12}>
                <Grid container sm={1} />
                <Grid container sm={5} className={classes.imgStyles}>
                    <img src={quizLogo1} height='180px' style={{margin: '10px', alignSelf:'center'}}/>
                    <img src={quizLogo3} />
                    <img src={quizLogo2} height='180px' style={{margin: '10px', alignSelf:'center'}}/>
                    
                    
                </Grid>
                <Grid container sm={5} className={classes.textStyles}>
                    <Typography style={{color: 'rgb(150, 150, 150)'}}>app design</Typography>
                    <Typography style={{color: 'rgb(150, 150, 150)'}}>app development</Typography>
                    <Typography style={{color: 'rgb(120, 120, 120)'}}>_______________</Typography>
                    <Typography variant='h3'><br/>quizApp</Typography>
                    <Typography variant='h5' style={{color: 'rgb(200, 200, 200)'}}><br/>designed and developed an online quiz app<br/>for university course</Typography>
                
                
                </Grid>
                <Grid container sm={1} />
            </Grid>
            
            
            <Grid container sm={12}>
                <Grid container sm={1} />
                <Grid container sm={5} className={classes.textStyles}>
                    <Typography style={{color: 'rgb(150, 150, 150)'}}>graphics design</Typography>
                    <Typography style={{color: 'rgb(120, 120, 120)'}}>_______________</Typography>
                    <Typography variant='h3'><br/>tShirtDesign</Typography>
                    <Typography variant='h5' style={{color: 'rgb(200, 200, 200)'}}><br/>designed a tShirt for a group</Typography>
                
                
                </Grid>
                <Grid container sm={5} className={classes.textStyles}>
                    <img src={tShirtLogo} />
                </Grid>
                <Grid container sm={1} />
            </Grid>

        </Grid>
        <Divider variant="fullWidth" style={{color:'rgb(0,0,0)', marginTop: '300px'}}/>
        
    </div>
    )
}